import React from 'react';
import PropTypes from 'prop-types';

export default class DateInput extends React.Component {

    constructor(props) {
        super(props);
        this.state = { value: this.editFormat(props.value), focused : false, valid : true };
        this.onFocus = this.onFocus.bind(this);
        this.onBlur = this.onBlur.bind(this);
        this.onChange =  this.onChange.bind(this);
    }

    componentWillReceiveProps(newProps){
        // console.log("In componentWillReceiveProps with newProps ==> ", newProps);
        // console.log("In componentWillReceiveProps oldState==> ", this.state);
        
        if(newProps.value !== this.props.value) {
            this.setState({ value : this.editFormat(newProps.value) });
        }
    }

    onFocus() {
        // console.log("In onFocus setting state true");
        this.setState({focused : true});
    }

    onBlur(e){            
        const value = this.unformat(this.state.value);
    //    console.log(" In onBlur, value: ", value)               
        const valid = this.state.value === '' || value != null;
        // console.log(" In onBlur, valid: ", valid)    
        // console.log(" In onBlur, state.valid", this.state.valid)  
        // console.log(" In onBlur, onValidityChange : ", this.props.onValidityChange)                                       
        if(valid != this.state.valid && this.props.onValidityChange) {
            this.props.onValidityChange(e, valid);
        }
        this.setState({ focused : false, valid});
        if(valid) this.props.onChange(e,value);        
    }

    onChange(e) {
        if (e.target.value.match(/^[\d-]*$/)) {
            // console.log("in Childs's OnChange ... event.target", e.target)                    
            this.setState({ value : e.target.value });
        }
    }

    displayFormat(date) {
        return (date != null) ? date.toDateString() : '';
    }
    
    editFormat(date) {
        return (date != null) ? date.toISOString().substr(0, 10) : '';
    }
    
    unformat(str) {
        // console.log(" In unformat, str: ", str)  ;         
        const val = new Date(str);
        // console.log(" In unformat, new Date(str) : ", val)  
        // console.log(" In unformat, val.getTime()", val.getTime())                    
        return isNaN(val.getTime()) ? null : val;
    }

    render() {
        // const className = (!this.state.valid && !this.state.focused) ? 'invalid' : null;
        const value = (this.state.focused || !this.state.valid) ? this.state.value : this.displayFormat(this.props.value);
                        //if outside box or date not valid ?  put state value     else  display value
        
        const childProps = Object.assign({}, this.props);
        delete childProps.onValidityChange;        
        return(
        <input
            // <input type="text" size={20} name={this.props.name} className={className} value={value} placeholder ={this.state.focused ? 'yyyy-mm-dd' : null}
            // onFocus={this.onFocus} onBlur={this.onBlur} onChange={this.onChange} />
            type="text" {...childProps} value={value}
            placeholder={this.state.focused ? 'yyyy-mm-dd' : null}
            onFocus={this.onFocus} onBlur={this.onBlur} onChange={this.onChange}  
        />
        );
    }
}

DateInput.propTypes = {
    value : PropTypes.object,
    onChange : PropTypes.func.isRequired,
    onValidityChange : PropTypes.func,
    name : PropTypes.string.isRequired,
};


